  font-size: 42px;
  float: left;
  height: 1px;
  top: -25px;
  position: relative;
  width: 32px;
  left: 5px;
  pointer-events: none;
  cursor: default;
}

.action-label {
  text-transform: capitalize;
}

table input[type="checkbox"] {
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
  margin: 0;
}

.error-panel {
  width: 100%;
  height: 50px;
  background-color: #260707;
  color: #c8999e;
}

.click-cue {
  width: 1em;
  height: 1em;
}



