



























































* {
  box-sizing: border-box;
}

.modal-header h3 {
  margin-top: 0;
  color: #a9b9ee;
}

.modal-body {
  margin: 20px 0;
}

.text-right {
  text-align: right;
}

.form-label {
  display: block;
  margin-bottom: 1em;
}

.form-label > .form-control {
  margin-top: 0.5em;
}

.form-control {
  display: block;
  width: 100%;
  padding: 0.5em 1em;
  line-height: 1.5;
  border: 1px solid #aabcfe;
}

.action-label {
  text-transform: capitalize;
}



